}
p:last-of-type {
  margin-bottom: 0;
}

abbr[title] {
  cursor: help;
  text-decoration: var(--flatify__txt-color-light) underline dotted 0.125em;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1em;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 2em;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1em;
}

ol ol,
ul ul,
ol ul,
ul ol {
