}

mark {
  color: var(--flatify__text-color-darker);
}
mark:not(.inline) {
  border-radius: calc(var(--flatify__border-radius) * 0.38);
  padding: 0 0.32em;
  background: var(--flatify__color-yellow-light);
}
mark.inline {
  --flatify__local-bg-color: var(--flatify__color-yellow-light);
  position: relative;
  white-space: nowrap;
  margin: 0 0.32em;
  background: transparent !important;
}
mark.inline:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: var(--flatify__local-bg-color);
  border-radius: calc(var(--flatify__border-radius) * 0.38);
  opacity: 0.9;
  transform: rotate(-3deg) scale(1.1, 1.2) skew(-10deg);
}

sub,
sup {
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a[href] {
  color: var(--flatify__link-color);
  text-decoration: var(--flatify__link-decoration);
  transition: color var(--flatify__simple-transition), background var(--flatify__simple-transition);
}
a[href]:hover {
  color: var(--flatify__link-color__hover);
  text-decoration: var(--flatify__link-decoration__hover);
}
a[href]:focus {
  color: var(--flatify__link-color__focus);
  text-decoration: var(--flatify__link-decoration__focus);
}

pre,
