  padding: 1em;
  margin-top: 0;
  margin-bottom: 1em;
  background: var(--flatify__bg-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__table-border-color);
  border-radius: var(--flatify__border-radius);
