code {
  vertical-align: middle;
  font-size: 0.85em;
  word-wrap: break-word;
  padding: 0.18em 0.32em;
  background: var(--flatify__bg-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__table-border-color);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
}
a > code {
