}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  float: left;
  width: 100%;
  padding: 0;
  margin-bottom: 0.5em;
  font-weight: var(--flatify__bold-font-weight);
  line-height: inherit;
}
