}

::-webkit-inner-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
}

[type=tel],
[type=url],
[type=email],
[type=number] {
  direction: ltr;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::file-selector-button {
  font: inherit;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

iframe {
  border: 0;
}

summary {
  display: list-item;
  cursor: pointer;
}

progress {
  vertical-align: baseline;
}

[hidden] {
  display: none !important;
}

:root {
  --flatify__blockquote-accent-color: var(--flatify__color-accent-primary);
}

blockquote {
  position: relative;
  z-index: 0;
  overflow: hidden;
  line-height: var(--flatify__large-line-height);
  margin: 0 0 var(--flatify__paragraph-mb);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
  border-style: solid;
  border-color: var(--flatify__blockquote-accent-color);
  border-width: 0 0 0 0.5em;
}
blockquote:not(.no-icon):before {
  content: " ";
  position: absolute;
  z-index: -1;
  opacity: 0.1;
  background-color: currentColor;
  mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  -webkit-mask: var(--flatify__blockquote-icon) no-repeat 50% 50%;
  filter: brightness(100%);
}
blockquote:not(.large) {
  font-size: 1em;
  line-height: var(--flatify__large-line-height);
  padding: 0.75em 1.25em;
  border-radius: calc(var(--flatify__border-radius) * 0.5) var(--flatify__border-radius) var(--flatify__border-radius) calc(var(--flatify__border-radius) * 0.5);
}
blockquote:not(.large):before {
  top: 1em;
  width: 7em;
  height: 7em;
  left: 1em;
}
blockquote.large {
  font-size: 1.25em;
  border-width: 0.5em 0;
  padding: 1.25em 0.75em;
  margin-top: 2em;
}
blockquote.large:before {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 15em;
  height: 15em;
  margin: auto;
}
blockquote.large:not(.text-align-fill) {
  text-align: center;
}
blockquote cite {
  display: block;
  font-size: 0.64em;
  font-style: normal;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  padding-top: 1.5em;
  opacity: 0.8;
}

figure {
  position: relative;
  width: max-content;
  max-width: 100%;
  height: max-content;
  margin: 0 0 1em;
  border-radius: var(--flatify__border-radius);
}
figure img {
  width: 100%;
  height: 100%;
  border-radius: var(--flatify__border-radius);
}
figure figcaption {
  font-size: 0.9em;
  padding: 1em 0;
}
figure .source {
  --flatify__local-border-color: var(--flatify__txt-color-light);
  position: absolute;
  top: 1em;
  font-size: 0.75em;
  line-height: 1;
  padding: 0.5em;
  color: var(--flatify__txt-color-inverted);
  background: var(--flatify__txt-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__local-border-color);
  border-radius: calc(var(--flatify__border-radius) * 0.75);
  right: 1em;
}
figure.fill {
  width: 100% !important;
}
figure.centered {
  margin-left: auto;
  margin-right: auto;
}
figure.overlay-caption {
  overflow: hidden;
}
figure.overlay-caption figcaption {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-left: 1em;
  padding-right: 1em;
  color: var(--flatify__txt-color-inverted);
  background: linear-gradient(0, var(--flatify__txt-color-dark), transparent);
}

hr {
  margin: 1em 0;
  opacity: 0.5;
  color: currentColor;
  background-color: currentColor;
  border: none;
  border-radius: var(--flatify__border-radius);
}
hr:not([size]) {
  height: var(--flatify__border-width);
}
hr.dots {
  line-height: 1;
  display: block;
  height: auto;
  padding: 2em;
  text-align: center;
  background: transparent;
}
hr.dots:before {
  content: "‌•••";
  letter-spacing: 1.4em;
  font-size: 1.4em;
  font-weight: bold;
}
hr.short {
  width: 9em;
  margin: 3em auto;
