kbd {
  --flatify__local-border-color: var(--flatify__bg-color-darker);
  vertical-align: middle;
  text-align: center;
  font-size: 0.85em;
  font-weight: normal;
  line-height: 1;
  display: inline-block;
  min-width: 1.75em;
  padding: 0.18em 0.32em 0.32em;
  margin: 0 0.25em;
  background: var(--flatify__bg-color-dark);
  border: var(--flatify__border-width) solid var(--flatify__local-border-color);
  border-radius: calc(var(--flatify__border-radius) * 0.5);
  box-shadow: inset 0 -0.125em 0 var(--flatify__local-border-color);
  will-change: transform;
  transition: all var(--flatify__simple-transition);
}
kbd:hover {
  box-shadow: inset 0 0 0 var(--flatify__local-border-color);
  transform: translateY(0.15em);
}
kbd kbd {
  padding: 0;
  background: transparent;
  border: none;
  box-shadow: none;
  transform: none !important;
}

:root {
