  --flatify__table-bg-color-darker: var(--flatify__bg-color-darker);
  --flatify__table-txt-color: var(--flatify__txt-color);
}

table {
  display: table;
  table-layout: fixed;
  caption-side: bottom;
  color: var(--flatify__table-txt-color);
  width: 100%;
}
table td,
